/*reset styles.scss*/
html,body,header,main,footer,section,div,nav,ul,ol,li,a,p,span,b,i,em,input,label,button,textarea,blockquote,br,canvas,code,dt,dd,del,ins,form,iframe,img,pre,sub,sup,table,td,th,tfoot,tr,video,audio,aside,article,h1,h2,h3,h4,h5,h6 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,body {
  width: 100%;
  height: 100%;
}
a {text-decoration: none;}
ul, li {list-style-type: none;}
button {outline: none;}