.BetsList{
    .BetsList__changer{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        background: #0C0918;
        padding: 5px;

        .BetsList__changer-btn{
            color: #ffffff;
            border-radius: 7px;
            padding: 9px 10px 8px 10px;
            border: none;
            background: none;
            font-weight: 700;
            font-size: 11px;
            &.active{
                background: #231D47;
            }
        }
    }
}