#root {
  width: 100%;
  height: 100%;
}

body {
  font-family: "SF Pro Display";
  color: #fff;
}

.btn {
  background: #231D47;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 3px;
}
.btn.btn_single {
  border: 4px solid #0C0918;
  width: -moz-fit-content;
  width: fit-content;
  min-width: 39px;
  min-height: 39px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@keyframes slideFrameX {
  100% {
    background-position-x: -164px;
  }
}
@keyframes slideFrameY {
  100% {
    background-position-y: 164px;
  }
}
@keyframes slideClouds {
  100% {
    background-position-x: -1000000px;
  }
}
@keyframes shrink {
  0% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}
@keyframes dlprxA {
  0% {
    opacity: 0;
    width: 0px;
  }
  100% {
    opacity: 1;
    width: 58px;
  }
}
@keyframes gKBsdr {
  100% {
    background-position-x: -1000000px;
  }
}
.Options .Options__block {
  margin-right: 13px;
}
.Options .Options__block:last-child {
  margin-right: 0px;
}
.Options .Options__block .Options__btn {
  margin-right: 10px;
  position: relative;
}
.Options .Options__block .Options__btn::after {
  position: absolute;
  content: "";
  display: block;
  background: #231D47;
  width: 1.7px;
  height: 18px;
  right: -7px;
  top: 50%;
  transform: translate(-50%, -50%);
}
.Options .Options__block .Options__btn:last-child {
  margin-right: 0px;
}
.Options .Options__block .Options__btn:last-child::after {
  content: unset;
}

.BetsList .BetsList__changer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  background: #0C0918;
  padding: 5px;
}
.BetsList .BetsList__changer .BetsList__changer-btn {
  color: #ffffff;
  border-radius: 7px;
  padding: 9px 10px 8px 10px;
  border: none;
  background: none;
  font-weight: 700;
  font-size: 11px;
}
.BetsList .BetsList__changer .BetsList__changer-btn.active {
  background: #231D47;
}/*# sourceMappingURL=styles.css.map */