.Options{
    .Options__block{
        margin-right: 13px;
        
        &:last-child{
        margin-right: 0px;
        }
        .Options__btn{
            margin-right: 10px;
            position: relative;
            &::after{
                position: absolute;

                content: '';
                display: block;
    
                background: #231D47;
                width: 1.7px;
                height: 18px;

                right: -7px;
                top: 50%;
                transform: translate(-50%, -50%);
            }

            &:last-child{
                margin-right: 0px;
                &::after{
                    content: unset;
                }
            }
        }
    }
}