#root{
    width: 100%;
    height: 100%;
}

body{
    font-family: 'SF Pro Display';
    color: #fff;
}
.btn{
    background: #231D47;
    color: #fff;
    border: none;
    border-radius: 8px;
    padding: 3px;
    &.btn_single{
        border: 4px solid #0C0918;
        width: fit-content;
        min-width: 39px;
        min-height: 39px;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
}

// 

@keyframes slideFrameX{
    100%{
        background-position-x: -164px
    }
}
@keyframes slideFrameY{
    100%{
        background-position-y: 164px
    }
}
@keyframes slideClouds{
    100%{
        background-position-x: -1e+06px;
    }
}
@keyframes shrink {
    0% {
        width: 100%;
    }
    100% {
        width: 0%;
    }
}
@keyframes dlprxA {
    0% {
        opacity: 0;
        width: 0px;
    }
    100% {
        opacity: 1;
        width: 58px;
    }
}
@keyframes gKBsdr {
    100% {
        background-position-x: -1e+06px;
    }
}

